<template>
  <div class="song-list-wrap">
    <div class="title-bar">
      <div class="bg-layer" ref="bgLayer"></div>
      <h2 class="title" v-html="title"></h2>
      <i class="icon iconfont icon-fanhui" @click="$router.back()"></i>
    </div>
    <div class="recommend-wrap" ref="recommendWrap">
      <scroll class="main" :data="discList" ref="main">
        <div>
          <div class="list-box">
            <ul>
              <li class="item" v-for="(item, index) in discList" :key="index" @click="selectItem(item)">
                <img class="avatar" width="60" height="60" v-lazy="item.imgurl" alt="">
                <div class="text">
                  <h3 class="name" v-html="item.creator.name"></h3>
                  <p class="desc" v-html="item.dissname"></p>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </scroll>
      <router-view></router-view>
      <loading v-show="!discList.length"></loading>
    </div>
  </div>
</template>
<script>
import {mapMutations} from 'vuex'
import { getDiscList } from '../../api/recommend'
import {ERR_OK} from '../../api/config'
export default {
  data() {
    return {
      discList: [],
      title: ''
    }
  },
  created() {
    this._getDiscList()
  },
  activated() {
    this.title = this.$route.query.title
  },
  methods: {
    _getDiscList() {
      getDiscList().then((res) => {
        if (res.code == ERR_OK) {
          this.discList = res.data.list
        }
      })
    },
    selectItem(item) {
      this.$router.push({path: `/disc`, query: {id: item.dissid}})
      this.setDisc(item)
    },
    ...mapMutations({
      setDisc: 'SET_DISC'
    })
  }
}
</script>
<style lang="scss" scoped>
  @import "../../common/scss/variable";
  @import "../../common/scss/mixin";
  .song-list-wrap {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: $color-background;
    .title-bar {
      position: absolute;
      left: 0;
      top: 0;
      z-index: 1;
      width: 100%;
      height: 40px;
      overflow: hidden;
      transform-origin: top;
      background-size: cover;
      border-bottom: 1px solid #f5f5f5;
      .bg-layer {
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        transform-origin: top;
        background-size: cover;
      }
      .title {
        position: relative;
        line-height: 40px;
        text-align: center;
        padding: 0 40px;
        @include dot;
        font-size: $font-size-large;
        color: $color-text;
      }
      .icon {
        position: absolute;
        left: 0;
        top: 0;
        padding: 9px;
        font-size: $font-size-large;
        color: $color-theme;
      }
    }
  }
   .recommend-wrap {
    position: absolute;
    left: 0;
    top: 40px;
    right: 0;
    bottom: 0;
    .main {
      height: 100%;
      overflow: hidden;
      .banner {
        position: relative;
        height: 0;
        width: 100%;
        padding-top: 40%;
        overflow: hidden;
      }
      .list-box {
        .item {
          display: flex;
          align-items: center;
          padding: 10px 20px 10px;
          .avatar {
            flex: 0 0 60px;
            width: 60px;
            margin-right: 20px;
          }
          .text {
            display: flex;
            flex-direction: column;
            justify-content: center;
            flex: 1;
            line-height: 20px;
            font-size: $font-size-medium;
            word-break: break-all;
            .name {
              margin-bottom: 10px;
              color: $color-text;
            }
            .desc {
              color: $color-text-d;
            }
          }
          &:not(:last-child) {
            border-bottom: 1px solid #f5f5f5;
          }
        }
      }
    }
  }
</style>



